<template>
    <div class="user-box">
        <div class="options">
            <div>职位名称<input type="text"></div>
            <div>职位类型
                <template>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                        v-for="item in occupationalType"
                        :key="item.type"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </div>
            <div style="margin:0 10px;">学历要求
                <template>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                        v-for="item in educationalRequirements"
                        :key="item.value2"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </div>
            <div>工作年限
                <template>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                        v-for="item in workingLife"
                        :key="item.value3"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </div>
            <div style="margin-right:20px;margin-left:20px;">
                工作状态
                <template>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                        v-for="item in workingCondition"
                        :key="item.value4"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </div>
             <div>更新时间
                <el-date-picker
                    v-model="value5"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期">
                </el-date-picker>
            </div>
            <div class="button-box">
                <span style="background:rgb(96,152,226);">查询</span>
                <span style="background:rgb(172,172,172)">重置</span>
            </div>
        </div>
        <div class="state-box">
            <div class="operation">
                <span>停用</span>
                <span>启用</span>
                <span>初始化密码</span>
                <span>批量修改用户角色</span>
            </div>
            <div class="add-btn">
                <span>新增账号</span>
            </div>
        </div>
        <div class="list-box">
            <div>
                 <el-table
                        ref="multipleTable"
                        :data="tableData3"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                        type="selection"
                        width="55">
                    </el-table-column>
                    <el-table-column
                        prop="username"
                        label="职位名称"
                        width="120">
                    </el-table-column>
                    <el-table-column
                        prop="name"
                        label="职位类别"
                        width="120">
                    </el-table-column>
                    <el-table-column
                        prop="corporatename"
                        label="城市">
                    </el-table-column>
                    <el-table-column
                        prop="tel"
                        label="学历要求">
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="工作年限">
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="更新时间">
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="状态">
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="操作">
                        <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
                        <el-button @click="handleClickDel(scope.row)" type="text" size="small">删除</el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="paging-box">
                <div>共{{ tableData3.length }}条</div>
                <div>
                    <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="10"
                        layout="prev, pager, next, jumper"
                        :total="tableData3.length">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            value6:'',
            occupationalType:[],//职业
            educationalRequirements:[],//学历要求
            workingLife:[],//工作年限
            workingCondition:[],//工作状态
            value: '',
             tableData3: [{
                    username:'test@qq.com',
                    date: '2016-05-03',
                    name: '王小虎',
                    corporatename: '超对称',
                    tel:'12345'
                }, {
                    username:'test@qq.com',
                    date: '2016-05-02',
                    name: '王小虎',
                    corporatename: '超对称',
                    tel:'12345'
                }, {
                    username:'test@qq.com',
                    date: '2016-05-04',
                    name: '王小虎',
                    corporatename: '超对称',
                    tel:'12345'
                }, {
                    username:'test@qq.com',
                    date: '2016-05-01',
                    name: '王小虎',
                    corporatename: '超对称',
                    tel:'12345'
                }],
            currentPage:1,
        }

    },
    methods:{
        handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //修改
       handleClick(row) {
      },
      //删除
       handleClickDel(row) {
      },
      
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
      }
    }
}
</script>
<style lang='scss' scoped>
    .user-box{
        .options{
            font-size: 16px;
           height: 158px;
           background: #fff;
           border-radius: 8px;
           padding: 15px 20px;
           display: flex;
           flex-wrap: wrap;
           div{
               line-height: 40px;
               input{
                width: 166px;
                height: 40px;
                border-radius: 4px;
                border: 1px solid #DCDFE6;
                margin-right: 20px;
                margin-left: 5px;
               }
               
           }
        }
        .el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner{
            width: 230px;
            font-size: 12px;
            margin-left: 5px;
        }
        .button-box{
            span{
                display: inline-block;
                width: 66px;
                height: 26px;
                text-align: center;
                line-height: 26px;
                font-size: 14px;
                border-radius: 13px;
                color: #fff;
                cursor: pointer;
            }
        }
        .state-box{
            line-height: 60px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;
            .operation{
                margin-left: 20px;
                span{
                    color: rgb(79,150,244);
                    margin-right: 15px;
                    cursor: pointer;
                }
            }
            .add-btn{
                margin-right: 20px;
                span{
                    display: inline-block;
                    width: 89px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    background: rgb(241,145,73);
                    border-radius: 15px;
                    cursor: pointer;
                    color: #fff;
                }
            }
        }
        .list-box{
            .paging-box{
                display: flex;
                justify-content: space-between;
                margin-top: 15px;
            }
        }
        
    }
</style>



